<template>
  <div class="c-hot-items">
    <h3 class="c-sidebar-right-title">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>
      <span>热门小册</span>
    </h3>
    <div class="m-hot-items">
      <router-link class="m-hot-item" v-for="(item, key) in hot_collections" :key="key"
                   :to="{ name: 'view', params: { collection_id: item.id } }">
        <div class="m-image">
          <img :src="item.image">
        </div>
        <div class="m-content">
          <div class="u-title">
            <i class="el-icon-notebook-2"></i>
            <span v-text="` ${item.title}`"></span>
          </div>
          <div class="u-excerpt" v-text="` ${item.excerpt}`"></div>
          <div class="u-rank" v-if="item.rank">
            <i class="el-icon-grape"></i>
            <span v-text="`七天 - ${item.rank['7days']}`"></span>
            <i class="el-icon-pear"></i>
            <span v-text="`三十天 - ${item.rank['30days']}`"></span>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
  import {get_collections} from "../service/collection";
  import {getRank} from "../service/stat";

  export default {
    name: "MyCollections",
    data() {
      return {
        hot_collections: null,
      }
    },
    mounted() {
      // 获取热门小册
      getRank().then((data) => {
        data = data.data;

        // 整理排名数据
        let ranks = [],
          item_ids = [];
        for (let i in data) {
          let item_id = this.$_.get(data, `${i}.name`);
          if (item_id) {
            item_ids.push(item_id);
            ranks[item_id] = this.$_.get(data, `${i}.value`, {});
          }
        }
        item_ids = item_ids.slice(0, 15);

        // 获取对应小册列表
        get_collections({ids: item_ids, limit: item_ids.length}).then(
          (data) => {
            data = data.data;
            if (data.code === 200) {
              let collections = data.data.data;
              let output = [];
              for (let i in collections) {
                let collection_id = this.$_.get(collections, `${i}.id`);
                if(!collections[i]) continue;
                collections[i].rank = collection_id ? this.$_.get(ranks, collection_id, null) : null;
                output.push(collections[i]);
              }
              this.hot_collections = output;
            }
          }
        );
      });
    }
  }
</script>

<style lang="less">
  @import "../assets/css/components/hot_collections.less";
</style>